import React, { Component } from 'react';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';


export default class ImageList extends Component<{ images: Array<{ img: string }>; height?: number }>{
  render() {
    const style: any = {
      flexWrap: 'nowrap',
      transform: 'translateZ(0)',
    };

    const innerStyle: any = {};

    if (this.props.height) {
      style.height = this.props.height + 'px';
      innerStyle.height = this.props.height + 'px';
    }


    return (
      <div style={{
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
      }}>
        <GridList cols={2.5} style={style}>
          {this.props.images.map(image => (
            <GridListTile key={image.img} style={innerStyle}>
              <img src={image.img} />
            </GridListTile>
          ))}
        </GridList>
      </div>
    )
  }
}